<template>
	<div class="live-keep-live2keep-all-success">
		<mt-cell title=""></mt-cell>
		<div class="success-contain">
			<mt-cell title=""></mt-cell>
			<mt-cell title="" :value="detail.name"></mt-cell>
			<mt-cell title="" :value="detail.liveAccount"></mt-cell>
			<mt-cell title="" :value="detail.money1"></mt-cell>
			<mt-cell title="" :value="detail.money2"></mt-cell>
			<mt-cell title="" :value="detail.type"></mt-cell>
			<mt-cell title="" :value="detail.date"></mt-cell>
			<mt-cell title="" :value="detail.date1"></mt-cell>
			<mt-cell title="" :value="detail.date2"></mt-cell>
			<mt-cell title="" :value="detail.rate"></mt-cell>
			<mt-cell title="" :value="detail.interest1"></mt-cell>
			<mt-cell title="" :value="detail.interest2"></mt-cell>
			<mt-cell title="" :value="detail.giveType"></mt-cell>
		</div>
		<div class="field-action">
			<mt-button @click.native="back" type="primary"></mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/live-keep';

export default {
	data() {
		return {
			detail: {
				name: '',
				liveAccount: '',
				money1: '',
				money2: '',
				type: '',
				date: '',
				date1: '',
				date2: '',
				rate: '',
				interest1: '',
				interest2: '',
				giveType: ''
			}
		};
	},
	methods: {
		back() {
			this.$router.push('/live-keep/keep2live');
		},
		successKeep2live(account) {
			service.successKeep2live({
				account: account
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.successKeep2live(this.$route.params.account);
	}
};

</script>

<style lang="scss" scoped>

.live-keep-live2keep-all-success{
	.mint-cell:after{
		height: 0;
	}
	.success-contain{
		border-top: 1px solid #d9d9d9;
		border-bottom: 1px solid #d9d9d9;
	}
}

</style>


