<template>
	<div class="live-keep-keep-search-result">
		<mt-cell title=""></mt-cell>
		<mt-cell title="" :value="detail.number"></mt-cell>
		<mt-cell title="" :value="detail.date"></mt-cell>
		<mt-cell title="" :value="detail.money1"></mt-cell>
		<mt-cell title="" :value="detail.money2"></mt-cell>
		<mt-cell title="" :value="detail.title"></mt-cell>
		<div class="field-action">
			<mt-button @click.native="back" type="primary"></mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/live-keep';

export default {
	data() {
		return {
			detail: {
				number: '',
				date: '',
				money1: '',
				money2: '',
				title: ''
			},
			money: ''
		};
	},
	watch: {
		money: function(val){
			if(val){
				this.submitStatus = true;
			}else{
				this.submitStatus = false;
			}
		}
	},
	methods: {
		back() {
			this.$router.push('/live-keep/keep-search');
		},
		getKeepSearchResult(account) {
			service.getKeepSearchResult({
				account: account
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.getKeepSearchResult(this.$route.params.account);
	}
};

</script>


