<template>
	<div class="live-keep-keep2live-list">
		<mt-cell class="product-title" title=""></mt-cell>
		<div class="products">
			<a @click="detail(item.id)" v-for="item in products" class="product-item" href="javascript:;">
				<mt-cell title="" :value="item.type | keepType"></mt-cell>
				<mt-cell title="" :value="item.account"></mt-cell>
				<mt-cell title="" :value="item.money | number(2) + ''"></mt-cell>
				<mt-cell title="" :value="item.date"></mt-cell>
				<mt-cell title="" :value="item.rate| number(8) + '%'"></mt-cell>
				<mt-cell title="" :value="item.date1"></mt-cell>
				<mt-cell title="" :value="item.date2"></mt-cell>
				<mt-cell v-show="item.type == 1" title="" :value="item.status"></mt-cell>
			</a>
		</div>
		<div class="field-action">
			<mt-button @click.native="back" type="primary"></mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/live-keep';

export default{
	data() {
		return {
			products: []
		};
	},
	methods: {
		detail(id) {
			this.$router.push(`/live-keep/keep2live/detail/${id}`);
		},
		back() {
			history.back();
		},
		getKeepListForAccount(account) {
			service.getKeepListForAccount({
				account: account
			}).then(data => {
				this.products = data.data.data;
			});
		}
	},
	created() {
		this.getKeepListForAccount(this.$route.params.account);
	}
};

</script>

<style lang="scss">
	
.live-keep-keep2live-list{
	.product-title{
		border-bottom: 1px solid #d9d9d9;
	}
	.product-item{
		display: block;
		border-bottom: 1px solid #d9d9d9;
	}
	.mint-field-other{
		top:50%;
	}
	.mint-cell-title{
		width: 4rem;
	}
	.mint-cell:after{
		height: 0;
	}
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}

</style>


