<template>
	<div class="live-keep-keep-search-detail">
		<mt-cell title=""></mt-cell>
		<mt-cell title="" :value="detail.type"></mt-cell>
		<mt-cell title="" :value="detail.fixed_account"></mt-cell>
		<mt-cell title="" :value="detail.name"></mt-cell>
		<mt-cell title="" :value="detail.currency"></mt-cell>
		<mt-cell title="" :value="detail.fixed_balance"></mt-cell>
		<mt-cell title="" :value="detail.period"></mt-cell>
		<mt-cell title="" :value="detail.interest"></mt-cell>
		<mt-cell title="" :value="detail.interest_date"></mt-cell>
		<mt-cell title="" :value="detail.exec_interest"></mt-cell>
		<mt-cell title="" :value="detail.transfer_deposit_flag"></mt-cell>
		<mt-cell title=""></mt-cell>
		<mt-cell title="" :value="detail.type"></mt-cell>
		<mt-cell title="" :value="detail.type"></mt-cell>
		<div class="field-action">
			<mt-button @click.native="back" type="primary"></mt-button>
			<mt-button @click.native="submit" type="primary"></mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/live-keep';

export default {
	data() {
		return {
			detail: {
				type: '',
				fixed_account: '',
				name: '',
				currency: '',
				fixed_balance: '',
				period: '',
				interest: '',
				interest_date: '',
				exec_interest: '',
				transfer_deposit_flag: ''
			}
		};
	},
	watch: {
		money: function(val){
			if(val){
				this.submitStatus = true;
			}else{
				this.submitStatus = false;
			}
		}
	},
	methods: {
		submit() {
			service.submitLive2keep()
			.then(data => {
				this.$router.push(`/live-keep/keep-search/result/${this.detail.fixed_account}`);
			});
		},
		back() {
			history.back();
		},
		getKeepSearchDetail(account) {
			service.getKeepSearchDetail({
				account: account
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.getKeepSearchDetail(this.$route.params.account);
	}
};

</script>


