<template>
	<div class="live-keep-keep-search-list">
		<mt-cell class="product-title" title=""></mt-cell>
		<div class="products">
			<a @click="detail(item.id)" v-for="item in accounts" class="product-item" href="javascript:;">
				<mt-cell title="/" :value="item.account"></mt-cell>
			</a>
		</div>
		<div class="field-action">
			<mt-button @click.native="prev" type="primary"></mt-button>
			<mt-button @click.native="next" type="primary"></mt-button>
		</div>
		<div class="field-action">
			<mt-button @click.native="back" type="primary"></mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/live-keep';

export default{
	data() {
		return {
			accounts: []
		};
	},
	methods: {
		detail(id) {
			this.$router.push(`/live-keep/keep-search/detail/${id}`);
		},
		back() {
			history.back();
		},
		getKeepSearchListForAccount(account) {
			service.getKeepSearchListForAccount({
				account: account
			}).then(data => {
				this.accounts = data.data.data;
			});
		}
	},
	created() {
		this.getKeepSearchListForAccount(this.$route.params.account);
	}
};

</script>

<style lang="scss">
	
.live-keep-keep-search-list{
	.product-title{
		border-bottom: 1px solid #d9d9d9;
	}
	.product-item{
		display: block;
		border-bottom: 1px solid #d9d9d9;
	}
	.mint-field-other{
		top:50%;
	}
	.mint-cell-title{
		width: 4rem;
	}
	.mint-cell:after{
		height: 0;
	}
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}

</style>


